  import CubePage from '../../components/cube-page.vue'
  import orderService from './components/service.vue'
  import orderInvoice from './components/invoice.vue'
  export default {
    name: 'ordersWrapper',
    data (){
      return {
        selected: false,
        selectedTwo: false,
        maskShowTwo: false,
        maskShow: false,
        remarks: '',//备注
        number: 1, //数量
        address: {}, //地址
        goodsarr: {},//商品信息
        express: {},
        total_price: 0,// 总金额
        orderInfor: {}
      }
    },
    components: {
      orderService,
      orderInvoice,
      CubePage
    },
    methods: {
      //确认订单
      rightSubClick(){
        let params = {}
        params.goods_id = this.$route.query.goods_id
        params.count = this.$route.query.count
        params.address_id = this.address.id
        this.http.post(this.ports.integral.points,params, res =>{
          this.$store.commit('changeLoading',false)
          console.log(res)
          this.showToastTxtOnly(res.msg)
          if(this.toastTime){
            this.toastTime.hide()
          }
          if(res.success){
            let data = res.data
            this.list = data.res
//            this.$store.commit('changeCartNum',this.cart)
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      },

      //地址选择
      checkAddress(){
        let goods_id = this.$route.query.goods_id
        let key = this.$route.query.key
        let count = this.$route.query.count
        this.$router.push({path: '/integralOrder/address',query:{
          goods_id: goods_id,
          key: key,
          count: count
        }})
      },
      //默认数据
      getOrdersData(){
        let checkAddress = this.$route.query.address
        let goods_id = this.$route.query.goods_id
        let count = this.$route.query.count
        this.number = count
        console.log(checkAddress)
        this.http.get(this.ports.integral.create+'?goods_id='+goods_id+'&count='+count, res =>{
          this.$store.commit('changeLoading',false)
          console.log(res)
          if(res.success){
            let data = res.data
            this.address = data.address
            if(checkAddress){
              this.address =JSON.parse(checkAddress)
            }
            this.goodsarr = data.goods
            this.total_price = data.total_price
            //订单提交基本数据
            let goods = []
//            this.goodsarr.forEach((even,i)=>{
//              goods.push(even.goods)
//              this.orderInfor.goods = goods
//            })
            this.orderInfor.address = this.address
//            console.log(this.orderInfor)
//            this.goods = data.goods
//            this.$store.commit('changeCartNum',this.cart)
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      },
      //备注信息
      remarksVal(val){
        console.log(this.orderInfor)
      },
      //确认发票信息
      changeInvoiceSub(val){
        if(!val){
          this.changeSelectedTwo(val)
        }else{
          let invoice = {}
          if(val.name==1){
            val.name = this.address.name
          }else{
            if(!val.unit){
              this.showToastTxtOnly('请输入单位名称')
              return false
            }
            if(!val.sbCode){
              this.showToastTxtOnly('请输入纳税人识别码')
              return false
            }
          }
          this.goodsarr.forEach((even,i)=>{
            if(even.goods.a=='a'){
              this.$set(even.goods,'invoice',val)
            }
          })
          this.changeSelectedTwo(false)

        }
      },
      //确认安装信息
      changeServiceClick(val){
          this.goodsarr.forEach((even,i)=>{
            if(even.goods.b=='b'){
              this.$set(even.goods,'ress',val)
            }
          })
          this.changeSelected(false)
      },
      //弹出发票，安装弹出窗
      confirmClick(val,item,index){
        const that = this
        if(val==='invoice'){
          this.maskShowTwo = true
          setTimeout( ()=>{
            that.selectedTwo = true
            this.goodsarr.forEach((even,i)=>{
              even.goods.a = 'b'
            })
            item.a = 'a'
          },200)
        }else{
          this.maskShow = true
          setTimeout( ()=>{
            this.goodsarr.forEach((even,i)=>{
              even.goods.b = 'a'
            })
            item.b = 'b'
            that.selected = true
          },200)
        }

      },
      changeSelected(val){
        this.selected = false
        const that = this
        if(!val){
          setTimeout( ()=>{
            that.maskShow = false
          },300)
        }
      },
      changeSelectedTwo(val){
        this.selectedTwo = val
        const that = this
        if(!this.selectedTwo){
          setTimeout( ()=>{
            that.maskShowTwo = false
          },300)
        }
      },
      showToastTxtOnly(text) {
        this.toast = this.$createToast({
          txt: text,
          type: 'txt'
        })
        this.toast.show()
      },
      reduce(){
        if(this.number<=1){
          this.showToastTxtOnly('受不了了，宝贝不能再减少了哦')
          return false
        }else{
          this.number -=1;
        }
      },
      //加法
      plus(){
        if(this.number>=10) {
          this.showToastTxtOnly('已经没有更多的库存了，下次再来吧')
          return false
        }else{
          this.number +=1
        }
      },
      //数量监听限制
      handleNumChange(){
        if(this.number>=10){
          this.showToastTxtOnly('超出范围')
          this.number = 10
        }
      },
    },
    mounted (){
      this.getOrdersData()
    }
  }



